import {constants} from './index';
import {fromJS} from 'immutable';
export const handleBeamInfo = (data) => ({
    type: constants.BEAM_INFO,
    beamInfo: fromJS(data)
});

export const getUserInfos=(data) => ({
    type: constants.USER_INFO,
    userInfo: fromJS(data)
});
export const getAuthCode=(data) => ({
    type: constants.AUTH_CODE,
    authCode: fromJS(data)
});
