import React, {PureComponent} from "react";
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import {Badge, Layout, Menu,message, Button} from "antd";
import AuthComponent from "../common/authComponent ";
import {HeaderWrapper, Logo, ProjectName, UserInfo, UserName} from "./style";
import {toJS,set} from 'immutable';
import {actionCreators as panelActionCreators} from "../common/store";
import routes from "../../routes/config";
import {getRemindCount,getDeptInfo} from '../../axios';
console.log(routes,'routes')
const {Header} = Layout;
const {SubMenu} = Menu;
const {menus} = routes;
const AuthMenuItem = AuthComponent(Menu.Item);
const AuthButton = AuthComponent(Button);
class cHeader extends PureComponent {
    state = {
        count: 0,
        responseCount:0,
        defaultSelected:'/app/dashboard',
    }
    render() {
        const {handleSideMenus, userInfo,beamInfo,authCode} = this.props;
        const userInfos = userInfo.toJS();
        const beamInfos = beamInfo.toJS();
        const authCodes = authCode.toJS();
        // console.log(authCodes,'authCode')
        const {count,defaultSelected} = this.state;
        return (
            <Layout>
                <HeaderWrapper>
                    <Header>
                        <Logo>
                            <i></i>
                        </Logo>
                        <ProjectName>
                            <span className="foreign-text">Luban  BeamSite</span>
                            <span className="chinese-text" title={beamInfos.name}>{beamInfos.name}</span>
                        </ProjectName>
                        <Menu
                            className="menu"
                            theme="light"
                            mode="horizontal"
                            defaultSelectedKeys={[defaultSelected]}
                        >
                            {menus.filter(item => (item.key != '/app/remind' && !item.isVisble)).map((item, index) => {
                                return (
                                    <AuthMenuItem source="route" authcode={item.id} authcodes = {authCodes.menuAuthCode} key={item.key}
                                        onClick={() => handleSideMenus(item.subs)}
                                        disabled={item.key == '/app/manage'}>
                                            <Link to={item.key}>{item.title}</Link>
                                    </AuthMenuItem>
                                );
                            })}
                        </Menu>
                        <Menu
                            mode="horizontal" className="subMenu"
                        >
                            {/*进度提醒*/}

                            {
                                count == 0 ?
                                <AuthMenuItem source="route" authcode="3bd40ee75c5ac5b17b4f96f35e3a6bc4" authcodes = {authCodes.menuAuthCode} onClick={() =>this.handleNoRemind(menus.find(item => (item.key == '/app/remind' && !item.isVisble)).subs)}>
                                    <AuthButton className="icon-clear" source="fun" authcode="77006001" authcodes ={authCodes.funAuthCode}>
                                        <Link to='/app/remind'>
                                            <Badge className="remind-wrapper">
                                                <i className="no-remind"></i>
                                            </Badge>
                                        </Link>
                                    </AuthButton>
                                </AuthMenuItem>
                                :<AuthMenuItem source="route" authcode="3bd40ee75c5ac5b17b4f96f35e3a6bc4" authcodes = {authCodes.menuAuthCode} onClick={() => this.handleRemind(menus.find(item => item.key == '/app/remind').subs)}>
                                    <AuthButton className="icon-clear" source="fun" authcode="77006001" authcodes ={authCodes.funAuthCode}>    
                                        <Link to='/app/remind'>
                                            <Badge className="remind-wrapper">
                                                <i className="remind"></i>
                                            </Badge>
                                        </Link>
                                    </AuthButton>
                                </AuthMenuItem>
                            }
                            {
                                /* <Menu.Item onClick={() =>this.handleNoRemind(menus.find(item => (item.key == '/app/remind' && !item.isVisble)).subs)}>
                                        <Link to='/app/remind'>
                                        <Badge className="remind-wrapper">
                                            <i className="no-remind"></i>
                                        </Badge>
                                        </Link>
                                    </Menu.Item> 
                                    :
                                    <Menu.Item
                                        onClick={() => this.handleRemind(menus.find(item => item.key == '/app/remind').subs)}>
                                        <Link to='/app/remind'>
                                            <Badge className="remind-wrapper">
                                                <i className="remind"></i>
                                            </Badge>
                                        </Link>
                                    </Menu.Item>*/
                            }
                            <SubMenu
                                title={
                                    <UserInfo>
                                        <a className="avatar-wrapper">
                                            <i className="avatar"></i>
                                        </a>
                                        <UserName>
                                            <p>{userInfos.realName}</p> <p> {userInfos.roleName} </p>
                                        </UserName>
                                    </UserInfo>
                                }

                            >
                                <Menu.Item>
                                    <span onClick={e => this.props.onLogout(e)}>退出登录</span>
                                </Menu.Item>
                            </SubMenu>
                        </Menu>
                    </Header>
                </HeaderWrapper>
            </Layout>
        );
    }

    componentDidMount() {
        $('.ant-menu-item').map((key,val)=>{//暂时解决f5刷新页面 store数据初始化问题
            let menuInfo = $(val).find('a').attr('href').split('#')[1];
            if (this.props.location.pathname.indexOf(menuInfo)!=-1){
                $(val).click();
                this.props.history.push(menuInfo);
            }
        })
        getRemindCount().then(res => {
            const count = res.data.data.count;
            this.setState({
                count: count,
                responseCount: count,
            });
        })
    }
    // 无提醒内容
    handleNoRemind(item){
        // console.log(item);
        const {responseCount}=this.state
        if(responseCount==0){
            message.info('无提醒内容，请检查Center后台提醒设置中的相关设置信息！')
        }
        this.props.handleSideMenus(item)
    }

    // 有提醒内容
    handleRemind(item){
        console.log(item,'item')
        const lookAuthCode = "77006001";
        if (!((this.props.authCode.toJS()).funAuthCode.includes(lookAuthCode))) {
            message.warning('当前用户没有权限')
            return false;
        }
        this.setState({
            count: 0,
        },()=>{
            this.props.handleSideMenus(item)
        });

    }
}

const mapStateToProps = state => {
    return {
        userInfo: state.getIn(["login", "userInfo"]),
        beamInfo: state.getIn(["login", "beamInfo"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};

const mapDispatchToProps = dispatch => {
    return {
        handleSideMenus(item){
            dispatch(panelActionCreators.handleSideMenus(item))
        }
    }

}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(cHeader);
