import axios from "axios";
import { message } from "antd";
import { HashRouter } from "react-router-dom";
axios.defaults.withCredentials = true;
/**
 * 公用get请求
 * @param url       接口地址
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
 */
export const get = ({ url,withCredentials}) => {
  return axios.get(url, {withCredentials});
};
/**
 * 公用post请求
 * @param url       接口地址
 * @param data      接口参数
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
 */
export const post = ({ url, data, headers,withCredentials }) => {
  return axios.post(url, data, { headers,withCredentials}).then(res => res.data);
};
/**
 * 公用delete请求
 * @param url       接口地址
 * @param data      接口参数
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
 */
export const remove = ({ url, data }) => {
  return axios.delete(url, { data }).then(res => res.data);
};
/**公用put请求 
 * @param url       接口地址
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
**/

export const put = ({ url, withCredentials }) => {
    return axios.put(url, {withCredentials});
};
// Add a request interceptor
axios.interceptors.request.use(
  config => {
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);

// Add a response interceptor
axios.interceptors.response.use(
  function(response) {
    const router = new HashRouter();
    if (response.status === 200) {
      if (response.data.code==401) {
        message.error("您的账号已掉线！");
        router.history.push("/");
      } else if(response.data.code==4008) {
        message.error("无客户端权限不能登录！");
        router.history.push("/");
      }
    }
    return response;
  },
  function(error) {
    message.error(error.response.data.message);
    return error;
  }
);
