import * as constants from './constants';
import {fromJS} from 'immutable'

const defaultState = fromJS({
    siderMenus:[],//左侧菜单列表
    historyInfos:{
        "areaId":"",
        "stage":0
    },//台座新增下拉参数记忆
    scheduleInfos:{
        bridgeId:'',
        bodyType:''
    }
})

export default (state = defaultState, action) => {
    switch (action.type) {
        case constants.CHANGE_SIDEMENUS:
            return state.set('siderMenus',action.siderMenus)
        case constants.CHANGE_PLATSETTINGADD:
            return state.set('historyInfos',action.historyInfos)
        case constants.CHANGE_SCHEDULESETTINGADD:
            return state.set('scheduleInfos',action.scheduleInfos)
        default:
            return state
    }
}