import  {constants} from './index';
export const handleSideMenus = (menus) => {
    return (dispatch) => {
        const action = {
            type: constants.CHANGE_SIDEMENUS,
            siderMenus: menus
        }
        dispatch(action)
    }
}
export const platSettingAddInfo = (itemInfo) => {
    return (dispatch) =>{
        const action = {
            type:constants.CHANGE_PLATSETTINGADD,
            historyInfos: itemInfo
        }
        dispatch(action)
    }
}
export const scheduleSettingAddInfo  = (itemInfo) => {
    return (dispatch) =>{
        const action = {
            type:constants.CHANGE_SCHEDULESETTINGADD,
            scheduleInfos: itemInfo
        }
        dispatch(action)
    }
}