import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { actionCreators } from "./store";
import { Button, Checkbox, Form, Icon, Input, message, Select } from "antd";
import { LoginBodyStyle, LoginFormWrapper } from "./style";
import md5 from "js-md5";
import Cookies from "js-cookie";
import * as api from "../../axios";
import routes from "../../routes/config";
const PRODUCT_ID = 77; //梁场产品id
const {menus} = routes;
class LoginForm extends PureComponent {
  state = {
    beamList: [],
    loading: false,
    isLoginSuccess: false,
    val: 0,
    hasRemember: false,
    userInfo: null //当前登录用户的信息
  };
  handleUserNameChange = () => {
    this.props.form.setFieldsValue({
      password: "",
      remember:false
    });
  };
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      const { username, password, remember, beamField } = values;
      const passParams = { password, username };
      //先判断cookie中是否用用户信息
      const { CookieUserName, CookiePassWord } = this.hasUserCookie();
      //当cookie的用户密码和输入框中一致 使用cookie中的  反之使用用户输入的
      if (CookiePassWord == password && CookieUserName == username) {
        passParams.username = CookieUserName;
        passParams.password = Cookies.get("LB-PassWord");
      } else {
        passParams.password = md5(password);
      }
      if (!err) {
        this.setState({ loading: true });
        if (this.state.isLoginSuccess) {
          //记住密码
          this.hanldeRememberPassWord(remember, username, password);
          localStorage.setItem('beamField',beamField)
          this.setState({ loading: false });
          //获取登录用户梁场信息
          const {data: { data }} = await api.getUserInfo(beamField);
          let authCode = {
            menuAuthCode:[],
            funAuthCode:[]
          }
          let currentSelectMenu = {
            id:"",
            path:""
          };
          this.state.userInfo.clientAuthGroupResultList.map((val,key)=>{
              if(val.id == 'e493929bc2f3211035e25649072676f2' || val.id == "3d1a764a62c7843b402b1cb93a13e83a" ||val.id == "a7540bffe7c5221239fba655c9ad3796" || val.id == "3fbe24c102c49f9f1f98a3f5f52c1746") {
                
              } else {
                if(val.id && val.parentId){
                    authCode.menuAuthCode.push(val.id);
                    authCode.menuAuthCode.push(val.parentId);
                }
               
              }
              if(val.authCodeResultList.length>0 && (val.id == 'e493929bc2f3211035e25649072676f2' || val.id == "3d1a764a62c7843b402b1cb93a13e83a" ||val.id == "a7540bffe7c5221239fba655c9ad3796" || val.id == "3fbe24c102c49f9f1f98a3f5f52c1746")){
                if( authCode.menuAuthCode.indexOf(val.id) ===-1  && val.id!=val.parentId && val.id){
                    authCode.menuAuthCode.push(val.id);
                    authCode.menuAuthCode.push(val.parentId);
                    if(currentSelectMenu.id === ''){
                        currentSelectMenu.id = val.id;
                    }
                }
              }
              if(val.authCodeResultList.length>0) {
                if(val.authCodeResultList){
                    val.authCodeResultList.map((val1,key1)=>{
                        if(authCode.funAuthCode.indexOf(val1.authCode) == -1) {
                            authCode.funAuthCode.push(val1.authCode)
                        }
                    })
                }
              }
          })
          /* menus.map((val,key)=>{
              if(val.subs){
                val.subs.map((subVal,subKey)=>{
                    console.log(subVal,'subVal')
                    if(subVal.id.indexOf(currentSelectMenu.id)!=-1 && subVal.subs) {
                        console.log(subVal.subs,'subVal.subs')
                        currentSelectMenu.path = subVal.subs[0].key
                    }
                })
              } else {
                if(val.id.indexOf(currentSelectMenu.id)!=-1) {
                    currentSelectMenu.path = val.key
                }
              }
          }) */
        //   console.log(authCode.funAuthCode,'funAuthCode')
          //同步用户梁场信息到redux
          this.props.handleBeamInfo({ ...data, ...this.state.userInfo });
          this.props.handleAuthCode(authCode);
          this.props.history.push("/app/dashboard");
          return;
        }
        //当登录pds登录成功 直接走以下函数
        this.hanldeBeamLogin(passParams);
      }
    });
  };
  hasUserCookie() {
    const CookieUserName = Cookies.get("LB-UserName");
    const CookiePassWordLength = Cookies.get("LB-PassWordLength");
    const CookiePassWord = Cookies.get("LB-PassWord")
      ? Cookies.get("LB-PassWord").substr(0, CookiePassWordLength)
      : 0;
    return { CookieUserName, CookiePassWordLength, CookiePassWord };
  }
  hanldeRememberPassWord(remember, username, password) {
    if (remember) {
        //先判断cookie中是否已经存储 已经存储不做设置
          const { CookieUserName, CookiePassWord } = this.hasUserCookie();
          console.log(CookieUserName,CookiePassWord)
          if(CookieUserName && CookieUserName.indexOf(username)!=-1 ){
            return;
          }
        //   if (CookieUserName && CookiePassWord) return;
        Cookies.set("LB-UserName", username);
        Cookies.set("LB-PassWord", md5(password));
        Cookies.set("LB-PassWordLength", password.length);
        } else {
        Cookies.remove("LB-UserName");
        Cookies.remove("LB-PassWord");
        Cookies.remove("LB-PassWordLength", password.length);
        }
    
  }
  //获取企业列表
  getCompanyList(passParams) {
    return api.getCompanyList(passParams).then(res => {
      if (res.data && res.data.items.length) {
        return res.data.items[0].epid;
      }
      this.beamLoginOut();
      this.setState({ loading: false })
      message.error(res.msg);
      return null;
    });
  } 
  //登入梁场
  beamLogin(params){
    return api.beamLogin(params).then(res=>{
      if (res.code==1) {
        return res.data
      }
    })
  }
  //获取梁场列表信息
  getBeamInfo() {
    api.getBeamInfo().then(res => {
      const beamList = res.data.data;
      if (res.data.code === 1) {
        if (beamList.length) {
          this.setState({
            beamList: beamList,
            loading: false,
            isLoginSuccess: true
          });
          this.props.form.setFieldsValue({
            beamField: beamList[0].k
          });
          return;
        }
        this.setState({
          loading: false,
          isLoginSuccess: false
        });
        //登出
        this.beamLoginOut()
        message.error("账号无梁场权限！");
      }
    });
  }
  beamLoginOut(){
    api.beamLoginOut().then(res=>{
      Cookies.remove('username');
      Cookies.remove('password');
    })
  }   
  //登录center并获取梁场列表
  async hanldeBeamLogin(passParams) {
    const epid = await this.getCompanyList(passParams);
    const userInfo = await this.beamLogin({...passParams,epid});
    userInfo && this.props.handleUserInfo(userInfo);
    userInfo && this.getBeamInfo();
    !userInfo && this.setState({ loading: false });
    this.setState({ userInfo });
  }
  componentDidMount() {
    const username = Cookies.get("LB-UserName");
    const password = Cookies.get("LB-PassWord");
    const passwordlength = Cookies.get("LB-PassWordLength");
    const userNameHistory = JSON.parse(localStorage.getItem("userInfo"));

    if (username && password) {
      this.props.form.setFieldsValue({
        username: username,
        password: password.substr(0, passwordlength),
        remember: true
      });
    } else {
      this.props.form.setFieldsValue({
        username: userNameHistory?userNameHistory.userName:"",
        password: "",
        remember: false
      });
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const { beamList } = this.state;
    return (
      <LoginBodyStyle>
        <LoginFormWrapper>
          <div>
            <p className="login-logo" />
            <p className="login-name" />
            <p className="login-text">欢迎您的登录</p>
          </div>

          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item>
              {getFieldDecorator("username", {
                rules: [
                  {
                    required: true,
                    message: "请输入您的账号"
                  },
                  {
                    min: 1,
                    max: 20,
                    message: "长度不在范围内"
                  }
                  // {   pattern: new RegExp("^\\w+$", "g"),   message: "用户名必须为字母或者数字" }
                ]
              })(
                <Input
                  onChange={this.handleUserNameChange}
                  prefix={
                    <a className="icon">
                      <i className="icon-user" />
                    </a>
                  }
                  placeholder="请输入您的账号"
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator("password", {
                rules: [
                  {
                    required: true,
                    message: "请输入您的密码"
                  },
                  {
                    min: 1,
                    max: 20,
                    message: "长度不在范围内"
                  }
                ]
              })(
                <Input
                  prefix={
                    <a className="icon">
                      <i className="icon-password" />
                    </a>
                  }
                  type="password"
                  placeholder="请输入您的密码"
                />
              )}
            </Form.Item>

            {beamList.length ? (
              <Form.Item>
                <div className="inputGroup">
                  <a className="icon auth-wrapper">
                    <i className="icon-beamfield" />
                  </a>
                  {getFieldDecorator("beamField", {
                    rules: [
                      {
                        required: true,
                        message: "Please select your gender!"
                      }
                    ]
                  })(
                    <Select
                      className="beamfield-select"
                      suffixIcon={<Icon type="caret-down" />}
                    >
                      {beamList.map(item => (
                        <Option value={item.k} key={item.v}>
                          {item.v}
                        </Option>
                      ))}
                    </Select>
                  )}
                </div>
              </Form.Item>
            ) : (
              ""
            )}

            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
              loading={this.state.loading}
            >
              登 录
            </Button>
            <Form.Item>
              {getFieldDecorator("remember", {
                valuePropName: "checked",
                initialValue: false
              })(<Checkbox className="remember-text">记住密码</Checkbox>)}
              <div className="remember-text login-form-forgot">
                <a
                  target="_blank"
                  href="http://passport.luban.com/luban-pass/html/phone-find-pass.html"
                >
                  忘记密码
                </a>
              </div>
            </Form.Item>
          </Form>
        </LoginFormWrapper>
      </LoginBodyStyle>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    //设置梁场信息
    handleBeamInfo(data) {
      dispatch(actionCreators.handleBeamInfo(data));
    },
    handleUserInfo(data) {
      dispatch(actionCreators.getUserInfos(data));
    },
    handleAuthCode(data) {
        dispatch(actionCreators.getAuthCode(data));
    }
  };
};

export default connect(
  null,
  mapDispatchToProps
)(Form.create({ name: "normal_login" })(LoginForm));
