import {constants} from './index';
import {fromJS} from 'immutable';
//持久化
const beamInfo = localStorage.getItem('beamInfo')?JSON.parse(localStorage.getItem('beamInfo')):{};
const userInfo = localStorage.getItem('userInfo')?JSON.parse(localStorage.getItem('userInfo')):{};
const authCode = sessionStorage.getItem('authCode')!== "undefined"?JSON.parse(sessionStorage.getItem('authCode')):{};
const defaultState = fromJS({
    beamInfo:beamInfo,
    userInfo:userInfo,
    authCode:authCode
})
export default (state = defaultState, action) => {
    switch (action.type) {
        case constants.BEAM_INFO:
            localStorage.setItem('beamInfo', JSON.stringify(action.beamInfo));
            return state.set('beamInfo', action.beamInfo);
        case constants.USER_INFO:
            localStorage.setItem('userInfo', JSON.stringify(action.userInfo));
            return state.set('userInfo', action.userInfo);
        case constants.AUTH_CODE:
            sessionStorage.setItem('authCode', JSON.stringify(action.authCode));
            return state.set('authCode', action.authCode);
        default:
            return state
    }
}