import { get, post, remove,put } from "./tools";
import * as config from "./config";
export const beamLogin = params =>
  post({
    url: config.BEAM_LOGIN,
    data: params
  });
export const beamLoginOut = () =>
  get({
    url: config.BEAM_LOGIN_OUT
  });
//获取企业列表
export const getCompanyList = params =>
  post({
    url: config.COMPANY_LIST,
    data: params
  });
//获取项目部下用户列表
export const getDepartmentUserList = departmentId =>
  get({
    url: `${config.USER_LIST}/${departmentId}`
  });
/* 梁场信息 beam*/
// GET/api/ibf/beam/select/{beamId}  登录时选择梁场
export const getUserInfo = beamId =>
  get({
    url: `${config.SELECT_BEAM}/${beamId}`
  });
// GET/api/ibf/beam/all 查询全部梁场
export const getBeamInfo = () =>
  get({
    url: config.BEAM_INFO_ALL
  });

/* 梁体、梁段信息*/
// POST /api/ibf/body 保存梁体
export const createBody = params =>
  post({
    url: config.BODY_CREATE,
    data: params
  });
// POST /api/ibf/body/query查询梁体
export const getBody = params =>
  post({
    url: config.BODY_GET,
    data: params
  });
// DELETE /api/ibf/body/batch-delete 删除梁体
export const deleteBody = params =>
  remove({
    url: config.BODY_DELETE,
    data: params
  });
// GET  /api/ibf/body/available/{bridgeId}/{typeId} 查询还没有进入生产的梁段
export const getBodyAvailable = bridgeId =>
  get({
    url: `${config.BODY_AVAILABLE}/${bridgeId}`
  });
// POST    /api/ibf/body/progress-list   梁段进度
export const getProgressLists = params =>
  post({
    url: config.BODY_PROGRESS_LIST,
    data: params
  });
// POST    /api/ibf/summary/total-progress   总进度
export const getTotalProgress = (params, month) =>
  post({
    url: `${config.BODY_TOTAL_PROGRESS}/${month}`,
    data: params
  });
//GET   /api/ibf/body/progress/detail/{logId}梁段进度日志查询
export const getProgressDetail = logId =>
    get({
        url: `${config.PROGRESS_DETAIL}/${logId}`
    });
// GET   /api/ibf/body/next/{bodyId}  查询梁段进度下一步
export const getBodyNext = bodyId =>
  get({
    url: `${config.BODY_NEXT}/${bodyId}`
  });
// POST   /api/ibf/body/next  提交梁段进入下一步
export const setBodyNext = params =>
  post({
    url: config.BODY_NEXT_STEP,
    data: params
  });
// POST   /api/ibf/body/is-unique  名称是否重复
export const getNameUnique = params =>
  post({
    url: config.BODY_UNIQUE,
    data: params
  });
// GET    /api/ibf/body/stored/{pedestalId}   查询存梁台座上的梁段
export const getBodyStored = pedestalId =>
  get({
    url: `${config.BODY_STORED}/${pedestalId}`
  });
/* 值班人员信息 */
// GET    /api/ibf/duty   查询值班人员
export const getStaffDuty = (start, end) =>
  get({
    url: `${config.STAFF_ON_DUTY}?beginDay=${start}&endDay=${end}`
  });
// /api/ibf/duty/single保存值班人员
export const setStaffDutySingle = (day, username) =>
  get({
    url:`${config.STAFF_ON_DUTY}/single?day=${day}&userName=${username}`
  });
// POST   /api/ibf/duty  保存值班人员
export const setStaffDuty = params =>
  post({
    url: config.STAFF_ON_DUTY,
    data: params
  });
// DELETE   /api/ibf/duty  删除值班人员
export const deleteStaffDuty = params =>
  remove({
    url: config.STAFF_ON_DUTY,
    data: params
  });
