/* 生产计划 */
// POST /api/ibf/pedestral 新增台座
export const createPedestral = params =>
  post({
    url: config.CREATED_PREDESTRA,
    data: params
  });
// GET /api/ibf/pedestral/list/{stage} 查询对应状态台座
export const getStagePedestral = stateId =>
  get({
    url: `${config.PEDESTAL_STAGE}/${stateId}`
  });
// GET /api/ibf/body/pedestral/{pedestralId}台座生产记录
export const getStageProduced = (stage, pedestalId) =>
  get({
    url: pedestalId
      ? `${config.STAGE_PRODUCED}/${stage}?pedestalId=${pedestalId}`
      : `${config.STAGE_PRODUCED}/${stage}`
  });
// POST /api/ibf/pedestral/query 查询台座
export const getPedestral = params =>
  post({
    url: config.GET_PREDESTRA,
    data: params
  });
export const getPedestralStatus = stage =>
  get({
    url: `${config.GET_PREDESTRA_STATUS}/${stage}`
  });
// DELETE /api/ibf/pedestral/batch-delete
// 批量删除
export const deletePedestral = params =>
  remove({
    url: config.DELETE_PREDESTRA,
    data: params
  });
/* BIM模型 */
export const getBimAll = (params) => get({url:config.BIM_ALL});
//查询当前梁场的状态统计
export const getBimSummary = (params) => get({url:config.BIM_SUMMARY});
//查询指定构件的属性--------------[梁场V1.1]
export const getBimCompAttr = (params) => get({url:`${config.BIM_LIST_COMP_ATTR}/${params.ppid}?floor=${params.floor}&handle=${params.handle}&projTypeStr=${params.projTypeStr}`});
//查询工程类别
// 工程类型（1:土建预算；2:安装预算；3:钢筋预算；4:Revit；5:Tekla；6:造价工程；7:Remiz(班筑客户端上传)；8:班筑家装（PDS客户端）；9:场布预算；10:Civil3D；11:Bentley；12:Rhino）
export const getBimClasses = (params) => get({url:`${config.BIM_CLASSES}/${params.ppid}/${params.projType}`});
//更新bim模型--------------------[梁场V1.1]
export const putBeatBim = (params)=> put({url:`${config.BIM_BEAT}/${params.ppid}`});
//查询当前梁场所属项目部信息[梁场V1.1]
export const getDeptInfo  = (params) => get({url:`${config.DEPT_INFO}`});
//查询当前梁场所属项目的节点信息（组织和临建工程）[梁场V1.1]
export const getListDeptNode = (params) => get({url:`${config.LIST_DEPT_NODE}`});
//查询构件树的状态
export const getPageComp = (params) =>get({url:`${config.PAGE_COMP}/${params.ppid}?pageIndex=${params.pageIndex}&pageSize=${params.pageSize}&isRefBeam=${params.isRefBeam}`});
//梁体状态统计接口[梁场V1.1]
export const getStaticisticsBodyStatus = (params) =>get({url:`${config.STATICISTICS_BODY_STATUS}`});
//梁场token获取
export const getMotorToken = (params)=>get({url:`${config.TOKEN_MOTOR}`})