import styled from "styled-components";
import LoginBg from '../../static/imgs/login-bg.png';
import loginLogo from '../../static/imgs/icon.png';

// 登录背景图
export const LoginBodyStyle = styled.div`
    width: 100%;
    height: 100%;
    background: url(${LoginBg}) no-repeat center;
    background-size:cover;
    background-attachment: fixed;
    position: fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

export const LoginFormWrapper = styled.div`
    position: absolute;
    top: 10%;
    right: 10%;
    width: 400px;
    height: 600px;
    padding: 30px;
    background-color: #001b76;
    box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.29);
    p {
      margin: 0 auto;
      text-align: center;
    }
    .login-logo {
      width: 49px;
      height: 55px;
      background: url(${loginLogo}) no-repeat -85px -4px;
      margin-bottom: 20px;
      margin-top: 30px;
    }
    .login-name {
      width: 279px;
      height: 26px;
      background: url(${loginLogo}) no-repeat 0 -77px;
      margin-bottom: 40px;
    }
    .login-text {
      font-size: 20px;
      color: #ffffff;
      margin-bottom: 20px;
    }
    input {
      height: 40px !important;
      padding-left: 50px !important;
    }
    .ant-select-auto-complete.ant-select .ant-select-selection--single {
    height: 40px !important;
    }
    i {
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-top: -3px;
    }
    .icon {
      display: inline-block;
      width: 34px;
      height: 18px;
      border-right: 1px solid #dcdee2;
    }
    .icon-user {
      background: url(${loginLogo}) no-repeat -136px -32px;
    }
    .icon-password {
      background: url(${loginLogo}) no-repeat -168px -32px;
    }
    .icon-beamfield {
      background: url(${loginLogo}) no-repeat -202px -32px;
    }
    .auth-wrapper {
      margin: 5px 2px 5px 6px;
    }
    .ant-form-explain {
      font-size: 12px;
      color: #fecc34;
    }
    .beamfield-select {
        margin-left: 10px;
        width: 84% !important;
        float: right;
      .ant-select-selection {
            width: 100%;
            border: none;
            &:active{
                box-shadow: #fff !important;
            }
      }
    }
    .ant-select-selection__rendered:focus {
      outline: none !important;
    }
    .inputGroup {
      width: 100%;
      height: 40px;
      background-color:#fff;
      border-radius: 4px;
      border: 1px solid #d9d9d9;
      padding: 5px;
    }
    .remember-text {
      color: #fff;
      font-size: 14px;
      cursor: pointer;
    }

    .login-form-forgot {
      float: right;
    }

    .login-form-button {
      width: 100%;
      font-size: 20px;
      height: 40px;
    }
`;
