import styled from 'styled-components';
import loginLogo from '../../static/imgs/icon.png';
import remindPic from '../../static/imgs/tx.gif';


export const HeaderWrapper = styled.div`
   Header{
       height: 80px;
       /* background-image: linear-gradient(90deg, 
          #5a5efd 0%, 
          #618afd 50%, 
          #67b6fd 100%), 
          linear-gradient(-79deg, 
          #5aa6f9 0%, 
          #4f6dfa 100%);
       background-blend-mode: normal, normal; */
       background:#003a91;
       padding: 0 25px 0 0;
       overflow: hidden;
       text-align:center;
       .menu {
           display:inline-block;
           height: 80px;
           line-height: 77px;
           background:transparent;
           border-bottom: none;
           .ant-menu-item-selected {
            font-weight: 700;
            color: #fff;
            border-bottom: 2px solid #5869DA;
           }
           a {
            color: #fff;
            &:hover {
                font-weight: 700;
                color: #fff;
            }
           }
         
       }
       .subMenu {
          float: right;
          background-color:transparent;
          padding: 10px;
          .ant-menu-item .anticon, .ant-menu-submenu-title .anticon {
              margin-right: 0;
          }
          .ant-menu-item-selected {
            border-bottom: none;
           }
          .remind-wrapper {
            height: 60px;
            line-height: 60px;
            .remind {
                display: inline-block;
                width: 30px;
                height: 30px;
                background: url(${remindPic}) no-repeat;
                cursor: pointer;
            }
            
            .no-remind{
                width: 24px;
                height: 24px;
                display: inline-block;
                background: url(${loginLogo}) no-repeat -238px -2px;
            }
          }
          
       }
   }
   
`;
export const Logo = styled.div`
    float:left;
    width: 80px;
    height: 75px;
    //margin-right: 20px;
    //margin-top: 12px;
    i {
        display: inline-block;
        width: 80px;
        height: 75px;
        background: url(${loginLogo}) no-repeat 0 0;
    }
`;

export const ProjectName = styled.div`
    float:left;
    margin-right: 50px;
    margin-top: 16px;
    span {
        display: block;
        text-align: left;
    }
    .foreign-text {
        font-family: Swiss721BT-Black;
        font-size: 18px;
        font-weight: 700;
        line-height: 24px;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #ffffff;
    }
    .chinese-text {
        font-size: 16px;
        font-stretch: normal;
        line-height: 24px;
        letter-spacing: 0px;
        color: #ffffff;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 206px;
        word-break: break-all;
    }
`;
export const UserInfo = styled.div`
    width: 200px;
    height: 60px;
    .avatar-wrapper {
        float:left;
        margin-top: 13px;
        .avatar {
           display: inline-block;
           width: 36px;
           height: 36px;
           background: url(${loginLogo}) no-repeat -291px -34px;
        }
    }
    
`;

export const UserName = styled.div`
    display: inline-block;
    height: 60px;
    width: 150px;
    margin-left: 5px;
    margin-top: 12px;
    text-align: left;
    p {
      font-size: 12px;
      line-height: 18px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #fff;
    }
`;